/*================================================
Default CSS
=================================================*/

@font-face {
  font-family: 'brush';
  src: url(/static/media/dry_brush.2a19dbe6.ttf) format('truetype');
}

:root {
  --fontFamily: 'Open Sans', sans-serif;
  --fontFamily2: 'Montserrat', sans-serif;
  --fontFamily3: 'brush', sans-serif;
  --mainColor: #910101;
  --optionalColor: #00a79D;
  --whiteColor: #ffffff;
  --blackColor: #000000;
  --paragraphColor: #666666;
  --fontSize: 16px;
  --transition: .5s;
}

body {
  margin: 0;
  padding: 0;
  color: #000000;
  color: var(--blackColor);
  font-size: 16px;
  font-size: var(--fontSize);
  font-family: 'Open Sans', sans-serif;
  font-family: var(--fontFamily);
}

.red {
  color: #910101;
  color: var(--mainColor);
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #000000;
  color: var(--blackColor);
  font-family: 'Montserrat', sans-serif;
  font-family: var(--fontFamily2);
  font-weight: 700;
}

.page-red-header {
  color: #ffffff;
  color: var(--whiteColor);
  font-family: 'brush', sans-serif;
  font-family: var(--fontFamily3);
  font-size: 3em;
  font-weight: 500;
  display: block;
  padding-top: 60px;
  text-align: center;
}

.custom>input {
  padding: 0;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 47px;
  text-transform: uppercase;
  text-align: left;
}

.custom>input::-webkit-input-placeholder {
  color: #98A1B9;
  font-size: 20px;
}

.custom>input:-ms-input-placeholder {
  color: #98A1B9;
  font-size: 20px;
}

.custom>input::placeholder {
  color: #98A1B9;
  font-size: 20px;
}

.custom-zap-row>input::-webkit-input-placeholder {
  color: #98A1B9;
}

.custom-zap-row>input:-ms-input-placeholder {
  color: #98A1B9;
}

.custom-zap-row>input::placeholder {
  color: #98A1B9;
}

a {
  transition: .5s;
  transition: var(--transition);
  color: #000000;
  color: var(--blackColor);
  text-decoration: none;
  outline: 0 !important;
  white-space: nowrap;
}

a:hover {
  color: #910101;
  color: var(--mainColor);
  text-decoration: none;
}

:focus {
  outline: 0 !important;
}

.d-table {
  width: 100%;
  height: 100%;
}

.d-table-cell {
  vertical-align: middle;
}

p {
  font-family: 'Open Sans', sans-serif;
  font-family: var(--fontFamily);
  color: #666666;
  color: var(--paragraphColor);
  font-size: 16px;
  font-size: var(--fontSize);
  margin-bottom: 15px;
  line-height: 1.8;
}

p:last-child {
  margin-bottom: 0;
}

.ptb-100 {
  padding-top: 100px;
  padding-bottom: 100px;
}

.pt-100 {
  padding-top: 100px;
}

.pb-100 {
  padding-bottom: 100px;
}

.ptb-70 {
  padding-top: 70px;
  padding-bottom: 70px;
}

.pt-70 {
  padding-top: 70px;
}

.pb-70 {
  padding-bottom: 70px;
}

.bg-f9f9f9 {
  background-color: #f9f9f9;
}

.bg-fffbf1 {
  background-color: #fffbf1;
}

.bg-fff0ee {
  background-color: #fff0ee;
}

.f-14 {
  font-size: 12px;
}

/*section-title*/
.section-title {
  text-align: center;
  max-width: 730px;
  margin-top: -6px;
  margin-left: auto;
  margin-right: auto;
  margin-bottom: 50px;
}

.section-title h2 {
  font-size: 34px;
  margin-bottom: 0;
}

.section-title h2 span {
  color: #05b169;
}

.section-title p {
  font-size: 16px;
  margin-top: 13px;
}

/*default-btn*/
.default-btn {
  border-radius: 5px;
  transition: .5s;
  transition: var(--transition);
  display: inline-block;
  position: relative;
  border: none;
  background-color: #910101;
  background-color: var(--mainColor);
  color: #ffffff;
  color: var(--whiteColor);
  padding-top: 12px;
  padding-left: 55px;
  padding-right: 30px;
  padding-bottom: 12px;
  font-family: 'Montserrat', sans-serif;
  font-family: var(--fontFamily2);
  font-size: 16px;
  font-size: var(--fontSize);
  font-weight: 600;
}

.default-btn i {
  top: 50%;
  left: 30px;
  position: absolute;
  font-weight: normal;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}

.default-btn:hover {
  box-shadow: 0 30px 50px rgba(107, 124, 147, 0.15);
  background-color: #00a79D;
  background-color: var(--optionalColor);
  color: #ffffff;
  color: var(--whiteColor);
}

/*form-control*/
.form-control {
  border-radius: 0;
  background-color: #f9f9f9 !important;
  box-shadow: unset !important;
  transition: .5s;
  transition: var(--transition);
  border: none !important;
  height: 50px;
  border-top: 2px solid #eeeeee !important;
  padding-left: 15px;
  color: #000000;
  color: var(--blackColor);
  font-family: 'Montserrat', sans-serif;
  font-family: var(--fontFamily2);
  font-size: 16px;
  font-size: var(--fontSize);
  font-weight: 500;
}

.form-control::-webkit-input-placeholder {
  color: #999999;
  -webkit-transition: .5s;
  transition: .5s;
  -webkit-transition: var(--transition);
  transition: var(--transition);
}

.form-control:-ms-input-placeholder {
  color: #999999;
  -ms-transition: .5s;
  transition: .5s;
  -ms-transition: var(--transition);
  transition: var(--transition);
}

.form-control::placeholder {
  color: #999999;
  transition: .5s;
  transition: var(--transition);
}

.form-control:focus {
  border-top-color: #910101 !important;
  border-top-color: var(--mainColor) !important;
}

.form-control:focus::-webkit-input-placeholder {
  color: transparent;
}

.form-control:focus:-ms-input-placeholder {
